export function fileDocument(ctx, node) {
	ctx.beginPath();
	const offset = node.rect.width / 5;
	ctx.moveTo(node.rect.ex, node.rect.y + offset);
	ctx.lineTo(node.rect.ex - offset, node.rect.y);
	ctx.lineTo(node.rect.x, node.rect.y);
	ctx.lineTo(node.rect.x, node.rect.ey);
	ctx.lineTo(node.rect.ex, node.rect.ey);
	ctx.lineTo(node.rect.ex, node.rect.y + offset);
	ctx.lineTo(node.rect.ex - offset, node.rect.y + offset);
	ctx.lineTo(node.rect.ex - offset, node.rect.y);
	ctx.closePath();
	ctx.fill();
	ctx.stroke();
}
