<template>
  <div>
    <div
      id="line-chart"
      style="height:260px"
    />
  </div>
</template>

<script>
import echarts from 'echarts';

const option = {
    grid: { // 图表外边距设置
        left: 60,
        right: 50,
        top: 60,
        bottom: 50,
    },
    tooltip: { // 鼠标悬浮时的提示格式化
        trigger: 'axis',
        formatter(params) {
            let result = `日期: ${params[0].axisValue}</br>`;
            params.forEach((item) => {
                const dotHtml = `<span style="display:inline-block;margin-right:5px;border-radius:10px;width:10px;height:10px;background-color:${item.color}"></span>`;
                result += `${dotHtml}${item.seriesName}: ${item.data}</br>`;
            });
            return result;
        },
    },
    xAxis: { // x轴配置
        type: 'category',
        boundaryGap: false,
        data: ['1', '2', '3', '4', '5', '6'],
        axisLine: { // 是否显示轴线
            lineStyle: {
                color: '#DDDDDD',
            },
        },
        axisTick: { // 是否显示刻度线
            lineStyle: {
                color: '#DDDDDD',
                length: 6,
            },
        },
        axisLabel: { // 标签
            color: () => '#999999',
            fontWeight: 400,
            fontFamily: 'PingFang SC',
            fontSize: 14,
            margin: 17,
            align: 'left',
        },
    },
    yAxis: {
        type: 'value',
        axisLine: { // 轴线
            show: false,
        },
        axisTick: { // 刻度线
            show: false,
        },
        splitLine: { // 分割线
            lineStyle: {
                color: '#DDDDDD',
                type: 'dotted',
            },
        },
        axisLabel: { // 标签
            color: () => '#999999',
            fontWeight: 400,
            fontFamily: 'PingFang SC',
            fontSize: 14,
            margin: 11,
        },
    },
    series: [{
        data: [1, 10, 20, 30, 40, 50],
        type: 'line',
        name: 'pc',
        symbol: 'none',
        color: '#52C41A',
        lineStyle: { // 折线颜色
            color: '#52C41A',
        },
    },
    {
        data: [5, 15, 25, 35, 45, 55],
        type: 'line',
        name: '小程序',
        symbol: 'none',
        color: '#1890FF',
        lineStyle: { // 折线颜色
            color: '#1890FF',
        },
    }],
};
export default {
    mounted() {
        const myChart = echarts.init(document.getElementById('line-chart'));
        myChart.setOption(option);
    },
};
</script>

