// 导入库
import { Topology, registerNode } from '@topology/core';
import { register as registerFlow } from '@topology/flow-diagram';
import { register as registerActivity } from '@topology/activity-diagram';
import {
    area,
    areaAnchors,
    areaIconRect,
    areaTextRect,
    comment,
    commentAnchors,
    fileDocument,
    fileDocumentAnchors,
    fileDocumentIconRect,
    fileDocumentTextRect,
} from '../topologyExtendShape';

// 注册图形库
const canvasRegister = () => {
    registerFlow();
    registerActivity();
    registerNode('area', area, areaAnchors, areaIconRect, areaTextRect);
    registerNode('fileDocument', fileDocument, fileDocumentAnchors, fileDocumentIconRect, fileDocumentTextRect);
    registerNode('comment', comment, commentAnchors);
};
canvasRegister();

export default Topology;
