<template>
  <div class="flow-left">
    <div class="card-panel">
      <div class="card-header title">
        流程节点
      </div>
      <div class="card-main">
        <div v-for="(item, index) in node" :key="index" :class="['node-item', item.class]"
             :draggable="item.data"
             @dragstart="onDrag($event, item)"
        ></div>
      </div>
    </div>
    <div class="card-panel">
      <div class="card-header title">
        功能节点
      </div>
      <div class="card-main">
        <div v-for="(item, index) in functionNode" :key="index" :class="['other-item', 'graph-item', item.class]"
             :draggable="item.data"
             @dragstart="onDrag($event, item)"
        >
        </div>
      </div>
    </div>
    <div class="card-panel">
      <div class="card-header title">
        辅助图形
      </div>
      <div class="card-main">
        <div v-for="(item, index) in auxiliaryGraph" :key="index" class="other-item"
             :draggable="item.data"
             @dragstart="onDrag($event, item)"
        >
          <i :class="['iconfont', item.icon]"></i>
          <div>{{ item.name }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { node, functionNode, auxiliaryGraph } from './js/const.js';

export default {
    data() {
        this.node = node;
        this.functionNode = functionNode;
        this.auxiliaryGraph = auxiliaryGraph;
        return {};
    },
    methods: {
        // 拖拽节点加入画布
        onDrag(event, DragedInfo) {
            event.dataTransfer.setData('Text', JSON.stringify(DragedInfo.data));
        },
    },

};
</script>

<style lang="scss" scoped>
.flow-left {
  width: 162px;
  height: inherit;
  min-width: 59px;
  overflow-x: hidden;
  overflow-y: auto;
  background: #f6f6f6;
  border-right: 1px solid #a2a2a2;
  .card-panel {
    .card-header {
      font-size: 12px;
      padding: 5px 10px;
      margin-bottom: 15px;
      border-bottom: 1px solid #ddd;
      &.title {
        color: #0d1a26;
        font-weight: 600;
      }
    }
  }
  .node-item {
    background-size: 100% 100%;
    margin: 0 auto 15px;
    width: 80%;
    height: 60px;
  }
  .graph-item{
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 70% 80%;
    height: 64px;
  }
  .other-item {
    margin-bottom: 10px;
    display: inline-block;
    width: 50%;
    color: #314659;
    text-align: center;
  }
  .iconfont {
      font-weight: normal;
      font-size: 40px;
      line-height: 1;
      display: inline-block;
      vertical-align: middle;
    }
  .flow-start{
    background-image: url('./img/start.png');
  }
  .flow-rectangle{
    background-image: url('./img/flow.png');
  }
  .flow-diamond{
    background-image: url('./img/judge.png');
  }
  .flow-end{
    background-image: url('./img/end.png');
  }
  .flow-notice{
    background-image: url('/img/notice.png');
  }
}
</style>
